import React, { Component } from 'react';
import AnchorButton from './AnchorButton';
import HeaderLink from './HeaderLink';
import './Header.css';

class Header extends Component {
	constructor(props) {
		super(props);
	}

	componentDidMount() {
		const header = document.getElementsByTagName("header")[0];
	    header.style.opacity = 0;
	    const unitEmersion = this.props.unitEmersion;
	    const timer = setTimeout(function() {
	    	unitEmersion(header, 300);
	    }, 800);
	}

	render() {
		return (
			<header>
				<div id="logo">
	          		<img src={require('../images/logo.svg')} alt="Huddle" id="main_logo_img" />
	          		<span id="main_logo_text">Degram</span>
	          	</div>
	          	<div id="header-links">
	          		{
	          			[
	          				{
	          					name: 'Product',
	          					href: "#"
	          				},
	          				{
	          					name: 'Portfolio',
	          					href: "#"
	          				},
	          				{
	          					name: 'Education',
	          					href: "#"
	          				}
	          			].map(link => <HeaderLink link={link} />)
	          		}
	          	</div>
	          	<AnchorButton href="#" type="small" >Profile</AnchorButton>
	        </header>
		);
	}
}

export default Header;

