import React from 'react';
import { footerUnits } from './footer-units';
import FooterSection from './FooterSection';
import './Footer.css';

const Footer = (props) => {
    const sections = footerUnits.map((unit, i) => {
      return (<FooterSection
        title={unit.title}
        links={unit.links}
        index={i}
        key={`FS-key-${i}`}
      />);
    })

    return(
      <footer>
            { sections }
      </footer>
    );
  
}

export default Footer;