import React, { Component } from 'react';
import './WhyDegram.css';

class WhyDegram extends Component {
	constructor(props) {
		super(props);
	}

	componentDidMount() {
		const article = document.getElementsByTagName("article")[0];
	    article.style.opacity = 0;
	    const unitEmersion = this.props.unitEmersion;
	    const scrollEmersion = this.props.scrollEmersion;
	    unitEmersion(article, 400);
	    const img = document.getElementsByTagName("img")[2];
	    scrollEmersion(img, unitEmersion);
	}

	render() {
		return (
			<article className="why-degram">
	          	<h2>Why Degram?</h2>
	          	<h6>
	          		Degram is a team of traders and analysts with 11 years of experience 
	          		in the financial markets. Our traders have helped thousands of clients 
	          		make money, and risk managers have made sure that customer deposits are safe.
	          	</h6>
	        </article>
		);
	}
}

export default WhyDegram;