import React, { Component } from 'react';

class HeaderLink extends Component {
	constructor(props) {
		super(props);
	}

	render () {
		return (
	        <a className="header-link">
	        	<img src={require(`./HeaderIcons/${this.props.link.name}.svg`)} />
	        	{" " + this.props.link.name}
	        </a>
		)
	}
}

export default HeaderLink;